import org.jetbrains.compose.resources.painterResource
import org.jetbrains.compose.resources.stringResource
import passaporteaupair.composeapp.generated.resources.Res
import passaporteaupair.composeapp.generated.resources.arrow_down
import passaporteaupair.composeapp.generated.resources.cd_faq_item_arrow_down
import passaporteaupair.composeapp.generated.resources.f1
import passaporteaupair.composeapp.generated.resources.f2
import passaporteaupair.composeapp.generated.resources.f3
import passaporteaupair.composeapp.generated.resources.f4
import passaporteaupair.composeapp.generated.resources.f5
import passaporteaupair.composeapp.generated.resources.f6
import passaporteaupair.composeapp.generated.resources.f7
import passaporteaupair.composeapp.generated.resources.f8
import passaporteaupair.composeapp.generated.resources.label_feedback_1
import theme.dimens
import utils.screenWidth
import kotlin.math.max
import kotlin.math.min


@OptIn(ExperimentalResourceApi::class, ExperimentalComposeUiApi::class)
@Composable
fun Feedback(modifier: Modifier) {
    val screenWidth = screenWidth

    Column(
        modifier = modifier
            .fillMaxWidth()
            .background(MaterialTheme.colorScheme.surface)
    ) {
        Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge * 2))
        Text(
            modifier = Modifier
                .fillMaxWidth(0.65f)
                .padding(start = screenWidth * 0.079f),
            text = stringResource(Res.string.label_feedback_1),
            style = MaterialTheme.typography.headlineMedium
        )
        Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.medium))
        val feedbacks = remember {
            listOf(
                Res.drawable.f1,
                Res.drawable.f2,
                Res.drawable.f3,
                Res.drawable.f4,
                Res.drawable.f5,
                Res.drawable.f6,
                Res.drawable.f7,
                Res.drawable.f8,
            )
        }
        val height = remember(screenWidth) { screenWidth * 0.322f }
        val itemWidth = remember(screenWidth) { screenWidth * 0.208f }
        val lazyRowState = rememberLazyListState()
        val scope = rememberCoroutineScope()
        Column {
            LazyRow(
                state = lazyRowState,
                userScrollEnabled = false,
                modifier = Modifier.fillMaxWidth().height(height),
                contentPadding = PaddingValues(horizontal = screenWidth * 0.079f),
                horizontalArrangement = Arrangement.spacedBy(MaterialTheme.dimens.spacing.extraLarge),
                verticalAlignment = Alignment.CenterVertically
            ) {
                feedbacks.forEach {
                    item {
                        Image(
                            modifier = Modifier.width(itemWidth).heightIn(max = height),
                            contentScale = ContentScale.Crop,
                            painter = painterResource(it),
                            contentDescription = null
                        )
                    }
                }
            }
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.large))

            Row(
                modifier = Modifier.fillMaxWidth(),
                horizontalArrangement = Arrangement.Center
            ) {
                Image(
                    modifier = Modifier
                        .size(MaterialTheme.dimens.iconSize.feedbackArrow)
                        .rotate(90f)
                        .clickable {
                            scope.launch {
                                lazyRowState.animateScrollToItem(
                                    max(
                                        lazyRowState.firstVisibleItemIndex - 1,
                                        0
                                    )
                                )
                            }
                        },
                    painter = painterResource(Res.drawable.arrow_down),
                    contentDescription = stringResource(Res.string.cd_faq_item_arrow_down),
                    colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.onBackground)

                )
                Spacer(modifier = Modifier.width(MaterialTheme.dimens.spacing.xxxLarge))
                Image(
                    modifier = Modifier
                        .size(MaterialTheme.dimens.iconSize.feedbackArrow)
                        .rotate(270f)
                        .clickable {
                            scope.launch {
                                lazyRowState.animateScrollToItem(
                                    min(
                                        lazyRowState.firstVisibleItemIndex + 1,
                                        feedbacks.size - 1
                                    )
                                )
                            }
                        },
                    painter = painterResource(Res.drawable.arrow_down),
                    contentDescription = stringResource(Res.string.cd_faq_item_arrow_down),
                    colorFilter = ColorFilter.tint(MaterialTheme.colorScheme.onBackground)
                )
            }
        }
    }

}